html * {
    padding: 0px;
    margin: 0;
    box-sizing: border-box;
}

$rounded: 20px;
$softed: 10px;

//color
$white: hsl(0, 0%, 100%);
$black: hsl(0, 0%, 5%);
$blue: #C3F8FA;
$yellow_light: #FAEEAA;
$yellow: hsl(51, 89%, 72%);
$transparent: #00000000;
$black_transparent: #00000078;
$pink: #FA91D8;
$pink_dark: #AD6D99;
$pink_light: hsl(319, 91%, 87%);
$red: #FAA69D;
$gray: #808080;

$max_container: 1300px;


//displays
$desktop: 1300px;
$to_desktop: 1299px;
$tablet: 900px;
$to_tablet: 899px;
$phone: 400px;
$to_phone: 399px;


//templates

@mixin box_d {
    margin: 45px 45px 120px;
    padding: 80px 180px;
    border-radius: $rounded;
}
@mixin box_t {
    margin: 35px 35px 100px;
    padding: 60px 120px;
    border-radius: $rounded;
}
@mixin box_p {
    margin: 25px 25px 80px;
    padding: 36px 60px;
    border-radius: $rounded;
}

